.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
    font-size: 20px;
    margin-top: 5px;
    height: 100%;
    
}
.dialogsItems{
    padding: 10px;
   
}
.dialog{
    border: 2px solid black;
    border-right: none;
    padding: 2px;
    text-align: center;
    margin: 2px;
   
}
.dialogsItems .active{
    color: gold;
}
.messages{
    padding: 10px;
    margin-left: -24px;
}
.messages>div{
    overflow: scroll;
    height: 240px;
    
}
.messages textarea{
    width: 250px;
    
}
.messages>button{
    position: relative;
    background: rgba(0, 0, 0, 0.288);
    margin-left: -75px;
    top: 20px;
    
}